<template>
    <div id="Prize">
        <ul>
            <li v-for="(p, i) in prizes.content" :key="i">{{ p.content }}</li>
        </ul>
    </div>
</template>
  
<script setup>
import { useStore } from "vuex";
import { reactive } from "vue";
const store = useStore()

var prizes = reactive({
    content: []
})
store.state.myAxios.get("Prize").then((res) => {
    setTimeout(() => {
        prizes.content = res.data.prizes || [];
    }, 700);
});
</script>
  
<style lang="less" scoped>
ul {
    color: var(--text);
    padding-left: 14px;
    min-height: 170px;
}
</style>