<template>
    <div id="Project">
        <div v-for="(p, i) in projects.content" :key="i">
            <div class="title">
                <el-icon :size="20">
                    <Edit />
                </el-icon>
                &nbsp;
                <span class="name">{{ p.name }}</span>
            </div>
            <p>{{ p.time }}</p>
            <p>
                <b>{{ p.job }}</b>
            </p>
            <div v-html="p.content"></div>
            <br />
        </div>
    </div>
</template>
  
<script setup>
import { useStore } from "vuex";
import { reactive } from "vue";
const store = useStore()

var projects = reactive({
    content: []
})
store.state.myAxios.get("Project").then((res) => {
    setTimeout(() => {
        projects.content = res.data.projects || [];
    }, 700);
});
</script>
  
<style lang="less" scoped>
#Project {
    color: var(--text);
    overflow: hidden;
    min-height: 170px;


    .title {
        display: inline-block;
        transition: var(--transition);
        transform: translateX(-35px);
    }

    .title:hover {
        transform: translateX(0);
    }

    .icon {
        font-size: 27px;
    }

    .name {
        position: relative;
        font-size: 27px;
        font-weight: 700;
    }
}
</style>