<template>
    <div id="Navigator" :style="'border-bottom-color:' + navBottomBorderColor">
        <div id="NavLeft">
            <el-icon id="NavMenuIcon">
                <Menu />
                <div id="NavMenu">
                    <NavMenuContent></NavMenuContent>
                </div>
            </el-icon>
        </div>
        <!-- <div id="NavMiddle"></div> -->
        <div id="NavRight">
            <el-icon class="nav-icon" @click="Click2Search">
                <Search />
            </el-icon>
            <el-icon class="nav-icon" @click="$store.commit('toggleMode')">
                <component :is="$store.getters.getModeIcon"></component>
            </el-icon>
            <el-icon class="nav-icon" :style="'width:' + topIconWidth + ';height:' + topIconWidth" @click="ToTop">
                <CaretTop />
            </el-icon>
        </div>
    </div>
</template>

<script setup>
import NavMenuContent from './Navigator/NavMenuContent.vue';
import { reactive, ref } from 'vue'
import { useStore } from 'vuex';
const store = useStore()
// 滚动监听
const navBottomBorderColor = ref('transparent')
const topIconWidth = ref('0')
function ScrollHandler() {
    // console.log(window.scrollY);
    if (window.scrollY > 0) {
        topIconWidth.value = '35px'
        navBottomBorderColor.value = 'var(--navigator-border)'
    } else {
        topIconWidth.value = '0'
        navBottomBorderColor.value = 'transparent'
    }
}
var ScrollHandlerThrottle = store.state.optimize.throttle(
    ScrollHandler,
    100
);
window.addEventListener('scroll', ScrollHandlerThrottle)
// 搜索按钮
function Click2Search() {
    window.open(
        `https://cn.bing.com`,
        "_blank",
        "noopener=yes,noreferrer=yes"
    );
}
// 回到顶部
function ToTop() {
    scrollTo(0, 0);
}
</script>

<style lang="less" scoped>
#Navigator {
    width: 100%;
    height: 60px;
    background: var(--bg-color);
    position: fixed;
    top: 0;
    border-top: transparent solid 1px;
    border-bottom: var(--navigator-border) solid 1px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    z-index: 7777;
    transition: var(--transition);
    flex-basis: 0;
    color: var(--text);

    #NavLeft,
    #NavRight {
        margin: 0 1.5rem;
        display: flex;
        align-items: center;
    }

    #NavLeft {
        position: static;

        #NavMenuIcon {
            position: static;
            width: 35px;
            height: 35px;
            display: flex;
            justify-content: center;
            align-items: center;
            margin: 0 4px;
            border-radius: 50%;
            color: var(--navigator-icon);
            font-size: 1.3em;
            aspect-ratio: 1/1;

            #NavMenu {
                position: absolute;
                top: 0;
                left: 27px;
                width: 330px;
                border-top: transparent solid 1px;
                border-bottom: var(--main-blue) solid 2px;
                border-radius: 0 0 17px 17px;
                z-index: 7777;
                background: var(--navigator-bg);
                transition: var(--transition);
                padding: 7px 17px;
                opacity: 0;
                pointer-events: none;
                transform: translateY(-10px);
                overflow: scroll;
                overscroll-behavior-y: contain;

                &:hover {
                    opacity: 1;
                    transform: translateY(0);
                    pointer-events: auto;
                }
            }

            &:hover {
                #NavMenu {
                    opacity: 1;
                    transform: translateY(0);
                    pointer-events: auto;
                }
            }
        }
    }

    #NavRight {
        .top-icon {
            width: 0;
            height: 0;
        }
    }

    .nav-icon {
        width: 35px;
        height: 35px;
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 0 4px;
        transition: var(--transition);
        border-radius: 50%;
        color: var(--navigator-icon);
        font-size: 1.3em;
        aspect-ratio: 1/1;
        cursor: pointer;
    }

    .nav-icon:hover {
        background: var(--primary-color);
        color: var(--text-hover);
    }

}
</style>