<template>
    <div id="Blog">
        <a v-for="(p, i) in posts.content" v-bind:key="i" class="post" :href="p.address" target="_blank"
            rel="noopener noreferrer">
            <div class="postCircle"></div>
            <div class="postContent">
                <div class="postTitle">
                    <h2>{{ p.title }}</h2>
                </div>
                <div class="postIntroduction">
                    <p>
                        {{ p.introduction }}
                    </p>
                </div>
                <div class="categoryAndTag">
                    <i class="el-icon-collection-tag"></i>
                    <div class="category">{{ p.category }}</div>
                    <i class="el-icon-price-tag"></i>
                    <div v-for="(t, i) in p.tags" v-bind:key="i" class="tag">
                        {{ t.tag }}
                    </div>
                </div>
                <div class="timeAndRead">
                    <div class="releaseTime">
                        <i class="el-icon-time"></i>
                        <span>{{ p.time }}</span>
                    </div>
                    <div class="readPost">
                        <span>阅读全文</span>
                        <svg class="icon" viewBox="0 0 32 32" aria-hidden="true">
                            <g fill="none" stroke="currentColor" stroke-linecap="round" stroke-linejoin="round">
                                <circle cx="16" cy="16" r="15.5"></circle>
                                <line x1="10" y1="18" x2="16" y2="12"></line>
                                <line x1="16" y1="12" x2="22" y2="18"></line>
                            </g>
                        </svg>
                    </div>
                </div>
            </div>
        </a>
    </div>
</template>

<script setup>
import { useStore } from "vuex";
import { reactive } from "vue";
const store = useStore()

var posts = reactive({
    content: []
})
store.state.myAxios.get("Blog").then((res) => {
    setTimeout(() => {
        posts.content = res.data.posts || [];
    }, 700);
});
</script>

<style lang="less" scoped>
#Blog {
    min-height: 170px;
    position: relative;
    margin: 0 auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.post {
    width: 47%;
    position: relative;
    margin: 0 0 17px 0;
    height: 270px;
    background: var(--module-bg);
    border-radius: 7px;
    /* border: 2px solid rgb(229, 230, 235); */
    border: 2px solid var(--module-border);
    transition: 0.3s;
    display: flex;
    text-decoration: none;
    box-shadow: 0 2px 4px rgb(0 0 0 / 14%);
}

.post * {
    color: var(--text);
}

.post:hover {
    border-color: var(--primary-color);
}

.post .postContent {
    width: 100%;
    height: 100%;
    color: #000;
    padding: 0 17px 0 17px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.post .postContent .postTitle {
    margin-top: 17px;
    height: 36px;
    overflow: hidden;
}

.post .postContent .postIntroduction {
    /* height: 70px; */
    overflow: hidden;
}

.post .postContent .postIntroduction p {
    overflow: hidden;
    text-overflow: ellipsis;
    word-wrap: break-word;
    margin: 0;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 3;
}

.post .postContent .categoryAndTag {
    height: 27px;
    overflow: hidden;
    display: flex;
    flex-shrink: 0;
}

.post .postContent .categoryAndTag svg {
    width: 27px;
    height: 27px;
}

.post .postContent .categoryAndTag .tag {
    color: #fff;
    padding: 0 7px;
    line-height: 27px;
    background: #007aff;
    margin: 0 3.5px;
    border-radius: 3px;
}

.post .postContent .categoryAndTag .category {
    color: #fff;
    padding: 0 7px;
    line-height: 27px;
    background: red;
    margin: 0 3.5px;
    border-radius: 3px;
}

.post .postContent .timeAndRead {
    position: relative;
    margin: 17px 0 17px 0;
    display: flex;
    justify-content: space-between;
    flex-shrink: 0;
}

.post .postContent .timeAndRead svg {
    width: 1.4em;
    height: 1.4em;
    vertical-align: -6px;
    margin-left: 3px;
}

.post .postContent .readPost {
    margin-right: 17px;
    border-bottom: 1px solid var(--text);
    padding: 0 7px 2px 7px;
    transition: 0.3s;
    font-weight: 700;
}

.post .postContent .readPost * {
    transition: 0.3s;
}

.post .postContent .readPost .icon circle {
    opacity: 0;
}

.post .postContent .readPost:hover {
    border-color: transparent;
}

.post .postContent .readPost:hover * {
    color: var(--main-blue) !important;
}

.post .postContent .readPost:hover .icon {
    transform: rotate(90deg);
}

.post .postContent .readPost:hover .icon circle {
    opacity: 1;
}

@media screen and (max-width: 900px) {
    .post {
        width: 100%;
    }
}
</style>