import { createStore } from 'vuex'

import myAxios from './modules/myAxios'
import optimize from './modules/optimize'
import Home from './modules/Home'

// 创建一个新的 store 实例
const store = createStore({
    state() {
        return {
            mode: sessionStorage.getItem('mode') || 'dark',
            startDate: {
                dateString: "2022/08/27",
                year: 2022,
                month: 8,
                date: 27
            },
        }
    },
    mutations: {
        toggleMode(state) {
            if (state.mode === 'dark') state.mode = 'light'
            else if (state.mode === 'light') state.mode = 'dark'
        }
    },
    getters: {
        getModeIcon(state) {
            return state.mode === 'dark' ? 'Moon' : 'Sunny'
        }
    },
    modules: {
        myAxios,
        optimize,
        Home
    }
})

export default store