<template>
    <div id="Education">
        <div id="container">
            <div v-for="(e, i) in educations.content" :key="i">
                <div class="icon">
                    <el-icon :size="20">
                        <School />
                    </el-icon>
                </div>
                <div class="content">
                    <span>{{ e.start }} ~ {{ e.end }}</span>
                    <h3>{{ e.name }}</h3>
                    <div v-html="e.content"></div>
                </div>
            </div>
        </div>
        <br />
    </div>
</template>

<script setup>
import { useStore } from "vuex";
import { reactive } from "vue";
const store = useStore()

var educations = reactive({
    content: []
})
store.state.myAxios.get("Education").then((res) => {
    setTimeout(() => {
        educations.content = res.data.educations || [];
    }, 700);
});
</script>

<style lang="less" scoped>
#Education {
    width: 70%;
    color: var(--text);
    min-height: 70px;

    #container {
        width: 100%;
        border-left: 2px solid #aaa;
        transform: translateX(20px);

        .icon {
            width: 40px;
            height: 40px;
            background: #dc143c;
            border-radius: 50%;
            text-align: center;
            display: flex;
            align-items: center;
            justify-content: center;
            transform: translateX(-20px) translateY(-20px);
        }

        .icon i {
            display: block;
            color: #fff;
            font-size: 21px;
        }

        .content {
            margin: 0 27px;
            transform: translateY(-20px);
            font-family: "Poppins", sans-serif;
            text-transform: capitalize;

            span {
                font-size: 17px;
            }

            h3 {
                font-size: 27px;
            }

            p {
                font-size: 14px;
            }

            & /deep/ a {
                text-decoration: none;
                color: #dc143c;
                font-size: 14px;
            }
        }
    }
}
</style>